.message{

margin: 10px;
width: fit-content;
padding: 10px;
flex-wrap: wrap !important ;

}

.message_user{
    
margin-left: auto;
text-align: center !important;
color: white;
}
.finalmessage{

    display: flex;
    align-items: center;

}

.post_avatar{
    margin-right: 20px;
    
}

.message_useCard{    
background-color: #0b81ff !important;
color: white !important;
flex-wrap: wrap !important ;
}
.message_guestCard{
background-color: #e9e9eb !important;
flex-wrap: wrap !important ;
}

.app_form{
    padding: 20px;
    position: fixed;
    bottom: 0;
    z-index: 1;
    width: 90%;
    background-color: #e9e9eb !important;
    margin: 40px;
        
    }
    .im{
        flex: 10% !important;
        justify-content: flex-end !important;
        outline: none;
      }

    .app_formcontrol{
        display: flex !important;
        flex-direction: row !important ;
    }

    .app_iconbutton{
        flex: 0 !important;
    }


    .app_input{
       flex: 50%; 
    }



    :root {
        --clr-bcg: #fff;
        --clr-font: #282c35;
        --clr-primary: #d23669;
      }
      
      .dark-theme {
        --clr-bcg: #282c35;
        --clr-font: #fff;
        --clr-primary: #ffa7c4;
      }
      .light-theme {
        --clr-bcg: #fff;
        --clr-font: #282c35;
        --clr-primary: #d23669;
      }