
.input-field [type=text][type=password]:focus {

    border-bottom: 1px solid rgb(8, 93, 252) !important;
    box-shadow:0 1px 0 0 rgb(8, 93, 252) !important; 
   }  
  
   
  .App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }
  
  .App-link {
    color: #61dafb;
  }


  .App{
    color: var(--clr-font);
  }



  :root {
    --clr-bcg: #fff;
    --clr-font: #282c35;
    --clr-primary: #d23669;
  }
  
  .dark-theme {
    --clr-bcg: #282c35;
    --clr-font: #fff;
    --clr-primary: #f3f1f2;
  }
  .light-theme {
    --clr-bcg: #fff;
    --clr-font: #282c35;
    --clr-primary: #394042;
  }

  .btn {
    background: var(--clr-primary) !important;
    color: var(--clr-bcg);
    padding: 0.25rem 0.5rem;
    border-radius: 5px;
    border-color: transparent;
    text-transform: capitalize;
    transition: all 0.3s linear;
    font-weight: bold;
    letter-spacing: 2px;
    cursor: pointer;
  }














